.loanObtaining {
  display: flex;
  align-items: center;
  background-color: rgba(4, 4, 4, 0.3);
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  display: none;
  max-height: 0;
  transition: all 0.2s;
}
.loanObtainingHolder {
  padding: 1em;
}
.loanObtaining.show {
  display: block;
}
.loanObtaining:global.slideUp {
  max-height: 5em;
}
.loanObtaining .loanObtainingButton {
  border: 1.5px solid #707070;
  background: linear-gradient(#ffffff, #c3c3c3 45%, #a8a8a8 55%, #b5b5b5);
  border-radius: 0.2em;
  cursor: pointer;
  color: #373742;
  padding: 0.3em 1em;
  font-size: 1.5em;
  width: auto;
  margin: auto;
  width: 100%;
  max-width: 17.5em;
}
@media only screen and (min-width: 768px) and (max-width: getSize(lg)) {
  .loanObtaining .loanObtainingButton {
    width: 50%;
  }
}
@media only screen and (max-width: getSize(md)) {
  .loanObtaining .loanObtainingButton {
    width: 100%;
    max-width: auto;
  }
}
.loanObtaining .loanObtainingButton:hover:not(:disabled) {
  color: #373742;
  background: linear-gradient(#d9d9d9, #9d9d9d 45%, #828282 55%, #8f8f8f);
}